0% {\n    transform: scale(1);\n    opacity: 0.7;\n  }\n  100% {\n    opacity: 0.0;\n  }\n}\n\n.loader {\n  position: relative;\n  transform: translateY(-25px);\n}\n\n.loader > div:nth-child(0) {\n  animation-delay: -0.8s;\n}\n\n.loader > div:nth-child(1) {\n  animation-delay: -0.6s;\n}\n\n.loader > div:nth-child(2) {\n  animation-delay: -0.4s;\n}\n\n.loader > div:nth-child(3) {\n  animation-delay: -0.2s;\n}\n\n.loader > div {\n  animation-fill-mode: both;\n  position: absolute;\n  top: -2px;\n  left: -26px;\n  width: 50px;\n  height: 50px;\n  border-radius: 100%;\n  border: 2px solid #fff;\n  animation: loader 1.25s 0s infinite cubic-bezier(0.21, 0.53, 0.56, 0.8);\n}\n",".search-bar {\n  input.search {\n    margin: 15px;\n    border-color: rgba(0, 0, 0, 0.2);\n    border-radius: 5px;\n    border-width: 1px;\n    font-size: 16px;\n    height: 42px;\n    line-height: 16px;\n    padding: 10px 12px;\n    width: 100%;\n\n    &:focus {\n      background: $white;\n      border-color: $pink;\n      color: $pink;\n      outline: 0;\n      outline: none;\n    }\n  }\n}\n\n@media (min-width: 768px) {\n  .search-bar {\n    input.search {\n      width: 25%;\n    }\n  }\n}\n"],"sourceRoot":"webpack://"}]);

